import { useState, useContext } from "react";
import { Container, Flex, Box, Text, Link as LinkText } from "@chakra-ui/react";
import { CloseIcon, HamburgerIcon } from "@chakra-ui/icons";
import { Link, useHistory } from "react-router-dom";
import { useAuth } from "../../stores/auth-context";

const MenuItem = ({ children, islast, to = "/" }) => {
  return (
    <Text
      mb={{ base: islast ? 0 : 8, sm: 0 }}
      mr={{ base: 0, sm: islast ? 0 : 8 }}
      display="block"
      fontSize="lg"
      fontWeight="bold"
    >
      <Link to={to}>{children}</Link>
    </Text>
  );
};

const Header = (props) => {
  const { currentUser, logout, roles } = useAuth();
  const [show, setShow] = useState(false);
  const history = useHistory();

  const handleLogout = (e) => {
    e.preventDefault();
    logout();
    history.push("/Quiz/");
  };

  return (
    <Flex as="nav" mb={4} p={4} bg={"blue.500"} color={"white"} {...props}>
      <Container maxW="container.lg">
        <Flex justify="space-between" align="center" wrap="wrap" {...props}>
          <Box>
            <Text fontSize="2xl" fontWeight="bold">
              <Link to="/Quiz/">Quiz</Link>
            </Text>
          </Box>

          <Box
            display={{ base: "block", md: "none" }}
            onClick={() => setShow(!show)}
          >
            {show ? <CloseIcon /> : <HamburgerIcon />}
          </Box>

          <Box
            display={{ base: show ? "block" : "none", md: "block" }}
            flexBasis={{ base: "100%", md: "auto" }}
          >
            <Flex
              align="center"
              justify={["center", "space-between"]}
              direction={["column", "row"]}
            >
              {currentUser && roles.isAdmin && (
                <MenuItem to="/Quiz/users">Users</MenuItem>
              )}
              {currentUser && (roles.isMod || roles.isAdmin) && (
                <MenuItem to="/Quiz/create-quiz">Create Quiz</MenuItem>
              )}
              {currentUser ? (
                <LinkText
                  style={{ textDecoration: "none" }}
                  fontSize="lg"
                  fontWeight="bold"
                  islast="true"
                  onClick={handleLogout}
                >
                  Logout
                </LinkText>
              ) : (
                <MenuItem to="/Quiz/auth" islast="true">
                  Sign In
                </MenuItem>
              )}
            </Flex>
          </Box>
        </Flex>
      </Container>
    </Flex>
  );
};

export default Header;
