import { useState, useRef } from "react";
import { Flex, Stack, Box, Heading, Link } from "@chakra-ui/layout";
import { Button } from "@chakra-ui/button";
import { Alert } from "@chakra-ui/alert";
import { FormControl, FormLabel } from "@chakra-ui/form-control";
import { Input } from "@chakra-ui/input";
import { useAuth } from "../stores/auth-context";

function Auth() {
  const [isLogin, setIsLogin] = useState(true);
  const [isLoading, setIsLoading] = useState(false);

  const emailInputRef = useRef();
  const firstNameInputRef = useRef();
  const lastNameInputRef = useRef();
  const passwordInputRef = useRef();
  const { signUp, login, error } = useAuth();

  const authSubmitHandler = (event) => {
    event.preventDefault();
    const enteredEmail = emailInputRef.current.value;
    const enteredPassword = passwordInputRef.current.value;

    setIsLoading(true);

    if (isLogin) {
      login(enteredEmail, enteredPassword);
    } else {
      const enteredLastName = firstNameInputRef.current.value;
      const enteredFirstName = lastNameInputRef.current.value;

      signUp(enteredEmail, enteredPassword, enteredLastName, enteredFirstName);
    }
    setIsLoading(false);
  };

  const authSwitchHandler = () => {
    setIsLogin((prevState) => !prevState);
    emailInputRef.current.value = "";
    if (firstNameInputRef.current != null) firstNameInputRef.current.value = "";
    if (lastNameInputRef.current != null) lastNameInputRef.current.value = "";
    passwordInputRef.current.value = "";
  };

  return (
    <Flex
      width="full"
      pt={{ base: 0, md: 12 }}
      align="center"
      justifyContent="center"
    >
      <Box
        borderWidth={{ base: 0, md: 1 }}
        width="full"
        maxWidth="500px"
        px={4}
        textAlign="center"
        borderRadius={4}
      >
        <Box my={4}>
          <form onSubmit={authSubmitHandler}>
            {error && <Alert status="error">{error}</Alert>}
            <Heading>{isLogin ? "Login" : "Register"}</Heading>
            <Box p={4}>
              <FormControl>
                {!isLogin && (
                  <Box>
                    <FormLabel pt={4} htmlFor="email">
                      First name
                    </FormLabel>
                    <Input
                      id="first-name"
                      type="text"
                      ref={firstNameInputRef}
                      placeholder="Enter your first name"
                      required
                    />
                    <FormLabel pt={4} htmlFor="email">
                      Last name
                    </FormLabel>
                    <Input
                      id="Last-name"
                      type="text"
                      ref={lastNameInputRef}
                      placeholder="Enter your last name"
                      required
                    />
                  </Box>
                )}
                <FormLabel pt={4} htmlFor="email">
                  Email address
                </FormLabel>
                <Input
                  id="email"
                  type="email"
                  ref={emailInputRef}
                  placeholder="Enter your email address"
                  required
                />
                <FormLabel htmlFor="password" pt={4}>
                  Password
                </FormLabel>
                <Input
                  id="password"
                  type="password"
                  ref={passwordInputRef}
                  placeholder="Enter your password"
                  required
                />
              </FormControl>
            </Box>
            <Button
              type="submit"
              disabled={isLoading}
              width="full"
              colorScheme="blue"
            >
              {isLoading
                ? "Sending request..."
                : isLogin
                ? "Sign In"
                : "Sign Up"}
            </Button>
            <Stack pt={2}>
              <Link onClick={authSwitchHandler}>
                {isLogin
                  ? "Create new account"
                  : "Login with an existing account"}
              </Link>
            </Stack>
          </form>
        </Box>
      </Box>
    </Flex>
  );
}

export default Auth;
