import {
  Center,
  FormControl,
  Input,
  Heading,
  FormLabel,
  Box,
  Button,
  RadioGroup,
  Radio,
  Text,
  useToast,
  Flex,
} from "@chakra-ui/react";
import { useRef, useState } from "react";
import { useHistory } from "react-router-dom";
import { realdatabase } from "../firebase";

const CreateQuiz = () => {
  const nameRef = useRef("");
  const toast = useToast();
  const history = useHistory();
  const [newChoices, setNewChoices] = useState([4]);
  const [choices, setChoices] = useState([[]]);
  const [questions, setQuestions] = useState(1);
  const [correctAns, setCorrectAns] = useState([]);
  const [formValues, setFormValues] = useState([{}]);

  const changeQuestionHandler = (i, e) => {
    let newFormValues = [...formValues];
    newFormValues[i][e.target.name] = e.target.value;
    setFormValues(newFormValues);
  };

  const updateChoices = (index, row, e) => {
    let newArr = [...choices];
    newArr[index][row] = e.target.value;
    setChoices(newArr);
  };
  const addFormFields = () => {
    setFormValues([...formValues, []]);
    setChoices([...choices, []]);
    setNewChoices([...newChoices, 4]);
    setCorrectAns([...correctAns, 1]);
    setQuestions((prevstate) => (prevstate += 1));
  };

  const removeFormFields = (i) => {
    let newFormValues = [...formValues];
    let choiceValues = [...choices];

    newFormValues.splice(i, 1);
    choiceValues.splice(i, 1);

    setFormValues(newFormValues);
    setChoices(choiceValues);
    setNewChoices((prevState) => prevState.slice(0, -1));
    setCorrectAns((prevState) => prevState.slice(0, -1));
    setQuestions((prevstate) => (prevstate -= 1));
  };

  const QuestionList = () => {
    let questionList = [];
    for (let i = 0; i < +questions; i++) {
      let answers = [];
      for (let j = 0; j < newChoices[i]; j++) {
        answers.push({
          id: String.fromCharCode(97 + j).toUpperCase(),
          name: choices[i][j],
          isCorrect: correctAns[i] == j ? true : false,
        });
      }

      questionList.push({
        question: formValues[i].question,
        answers,
      });
    }
    return questionList;
  };

  const handleAddChoices = (row) => {
    if (newChoices[row] < 5) {
      let tempChoices = [...newChoices];
      tempChoices[row]++;
      setNewChoices(tempChoices);
    }
  };
  const handleRemoveChoices = (row) => {
    if (newChoices[row] > 2) {
      let tempChoices = [...newChoices];
      tempChoices[row]--;
      setNewChoices(tempChoices);
    }
  };

  const updateCorrectAns = (i, e) => {
    let correctAnswers = [...correctAns];
    correctAnswers[i] = e.target.value;
    setCorrectAns(correctAnswers);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    const enteredName = nameRef.current.value;

    QuestionList();
    realdatabase.ref().child("/list").push({
      name: enteredName,
      questions: QuestionList(),
    });
    toast({
      position: "bottom-left",
      title: "Quiz created",
      description: "Quiz was succesfully created.",
      status: "success",
      duration: 5000,
      isClosable: true,
    });
    history.replace("/Quiz/");
  };

  return (
    <Center py={9} flexDirection="column">
      <Heading>Create Quiz</Heading>
      <Box width={700}>
        <FormControl>
          <form onSubmit={handleSubmit}>
            <FormLabel htmlFor="name" id="name">
              Quiz Name
            </FormLabel>
            <Input
              required
              ref={nameRef}
              id="name"
              type="text"
              placeholder="Enter quiz name"
              mb={10}
              width={300}
            />
            <Text align="center" fontSize="3xl" fontWeight="bold">
              Questions
            </Text>
            {formValues.map((element, index) => (
              <Box pb={20} className="form-inline" key={index}>
                <Box>
                  <Box mb={10}>
                    <FormLabel>Question</FormLabel>
                    <Input
                      required
                      type="text"
                      name="question"
                      value={element.question || ""}
                      onChange={(e) => changeQuestionHandler(index, e)}
                    />
                  </Box>

                  <RadioGroup name={`choice${index}`}>
                    <Box display="flex" justifyContent="space-between">
                      {Array(newChoices[index])
                        .fill(1)
                        .map((_, row) => (
                          <Flex>
                            <Box
                              key={row}
                              display="flex"
                              flexWrap="nowrap"
                              flexDirection="column"
                            >
                              <FormLabel>{`Choice ${row + 1}`}</FormLabel>

                              <Input
                                required
                                type="text"
                                name="choice"
                                value={choices[index][row]}
                                onChange={(e) => updateChoices(index, row, e)}
                                width={40}
                              />
                              <Radio
                                isRequired
                                value={`${row}`}
                                onClick={(e) => updateCorrectAns(index, e)}
                              >
                                Correct answer
                              </Radio>
                            </Box>
                          </Flex>
                        ))}
                    </Box>
                  </RadioGroup>

                  <Button
                    mr={6}
                    className="button add"
                    type="button"
                    onClick={() => handleAddChoices(index)}
                  >
                    Add Choice
                  </Button>
                  <Button
                    mr={6}
                    className="button add"
                    type="button"
                    onClick={() => handleRemoveChoices(index)}
                  >
                    Remove Choice
                  </Button>
                </Box>
                {index ? (
                  <Button
                    mt={30}
                    type="button"
                    className="button remove"
                    onClick={() => removeFormFields(index)}
                  >
                    Remove
                  </Button>
                ) : null}
              </Box>
            ))}
            <div className="button-section">
              <Button
                mr={6}
                className="button add"
                type="button"
                onClick={addFormFields}
              >
                Add question
              </Button>
              <Button className="button submit" type="submit">
                Submit
              </Button>
            </div>
          </form>
        </FormControl>
      </Box>
    </Center>
  );
};

export default CreateQuiz;
