import React from "react";
import { Route, Redirect } from "react-router-dom";
import { useAuth } from "./stores/auth-context";

export function PrivateModRoute({ component: Component, ...rest }) {
  const { currentUser, roles } = useAuth();

  return (
    <Route
      {...rest}
      render={(props) => {
        return (roles.isMod || roles.isAdmin) && currentUser ? (
          <Component {...props} />
        ) : (
          <Redirect to="/Quiz/" />
        );
      }}
    ></Route>
  );
}

export function PrivateRoute({ component: Component, ...rest }) {
  const { currentUser, roles } = useAuth();

  return (
    <Route
      {...rest}
      render={(props) => {
        return roles.isAdmin && currentUser ? (
          <Component {...props} />
        ) : (
          <Redirect to="/Quiz/" />
        );
      }}
    ></Route>
  );
}
