import {
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  Select,
  Container,
} from "@chakra-ui/react";
import { db } from "./firebase";
import { useEffect, useState } from "react";
import { useAuth } from "../stores/auth-context";

const Users = () => {
  const { currentUser } = useAuth();
  const [users, setUsers] = useState([]);

  useEffect(() => {
    db.collection("users")
      .get()
      .then(function (snapshot) {
        snapshot.forEach(function (doc) {
          setUsers((prevState) => [...prevState, doc.data().userInfo]);
        });
      });
  }, []);

  const selectRoleValue = (role) => {
    return role.isAdmin ? "admin" : role.isMod ? "mod" : "user";
  };

  const selectInputHandler = (event, row) => {
    let userData = [...users];

    const newValue = event.target.value;
    if (newValue === "mod") {
      userData[row].roles[0].isAdmin = false;
      userData[row].roles[0].isMod = true;
    }
    if (newValue === "admin") {
      userData[row].roles[0].isAdmin = true;
      userData[row].roles[0].isMod = false;
    }
    if (newValue === "user") {
      userData[row].roles[0].isAdmin = false;
      userData[row].roles[0].isMod = false;
    }
    setUsers(userData);

    const userInfo = userData[row];
    const userRef = db.collection("users").doc(userData[row].uid);
    userRef.update({
      userInfo,
    });
  };

  return (
    <Container>
      <Table variant="simple">
        <Thead>
          <Tr>
            <Th>Name</Th>
            <Th>Email</Th>
            <Th>Role</Th>
          </Tr>
        </Thead>
        <Tbody>
          {users !== null &&
            users.map((quiz, row) => (
              <Tr key={row}>
                <Td>{`${quiz.firstName + " " + quiz.lastName}`}</Td>
                <Td>{`${quiz.email}`}</Td>

                <Td w="full">
                  <Select
                    disabled={quiz.uid === currentUser.uid}
                    width={170}
                    onChange={(e) => selectInputHandler(e, row)}
                    defaultValue={selectRoleValue(quiz.roles[0])}
                  >
                    <option value="admin">Administrator</option>
                    <option value="mod">Moderator</option>
                    <option value="user">User</option>
                  </Select>
                </Td>
              </Tr>
            ))}
        </Tbody>
      </Table>
    </Container>
  );
};

export default Users;
